<template>
  <div class="goods" v-if="shopInfo">
    <!-- 推荐商品 -->
    <div
      class="recommend"
      v-for="(recommend, i) in shopInfo.recommend"
      :key="i"
    >
      <p class="recommend-name">{{ recommend.name }}</p>
      <div class="recommend-wrap">
        <ul>
          <li v-for="(item, i) in recommend.items" :key="i">
            <img :src="item.image_path" alt="" />
            <div class="recommend-food">
              <div class="recommend-food-name">{{ item.name }}</div>
              <div class="recommend-food-zm">
                月售{{ item.month_sales }} 好评{{ item.satisfy_rate }}%
              </div>
              <div class="recommend-food-price">
                <p>￥{{ item.activity.benefit_text }}</p>
                <CartControll :food="item" />
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!-- 商品分类 -->
    <div class="menuview">
      <!-- 左侧列表 -->

      <div class="menu-wrapper" ref="menuScroll">
        <ul>
          <li
            @click="selectmenu(i)"
            v-for="(item, i) in shopInfo.menu"
            :key="i"
          >
            <img v-if="item.icon_url" :src="item.icon_url" alt="" />
            <span>{{ item.name }}</span>
          </li>
        </ul>
      </div>
      <!-- 右侧类别 -->
      <div class="foods-wrapper" ref="foodScroll">
        <ul>
          <!-- 内容上 -->
          <li
            class="foods-list-hook"
            v-for="(item, i) in shopInfo.menu"
            :key="i"
          >
            <div class="category-title">
              <strong>{{ item.name }}</strong>
              <span class="">{{ item.description }}</span>
            </div>
            <!-- 内容下 -->
            <div
              class="fooddetails"
              @click="showfood(foods)"
              v-for="(foods, i) in item.foods"
              :key="i"
            >
              <img :src="foods.image_path" alt="" />
              <div class="fooddetails-info">
                <h4>{{ foods.name }}</h4>
                <p class="fooddetails-des">{{ foods.description }}</p>
                <p class="fooddetails-sales">
                  月售{{ foods.month_sales }} 好评率{{ foods.satisfy_rate }}%
                </p>
                <div class="fooddetails-price">
                  <div class="price">￥{{ foods.activity.fixed_price }}</div>
                  <CartControll :food="foods" />
                </div>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!-- 购物车 -->
    <ShopCar :shopInfo="shopInfo" />
    <!-- 商品详情 -->
    <Food @close="isShow = false" :foods="foods" :isShow="isShow" />
  </div>
</template>

<script>
import ShopCar from "./ShopCar.vue";
import BScroll from "better-scroll";
import CartControll from "@/components/Shop/CartControll.vue";
import Food from "./Food.vue";
export default {
  data() {
    return {
      shopInfo: null,
      menuScroll: {},
      foodScroll: {},
      foods: null,
      isShow: false,
    };
  },
  methods: {
    showfood(foods) {
      this.foods = foods;
      this.isShow = true;
    },
    getDate() {
      this.$axios.get("/api/profile/batch_shop").then((res) => {
        res.data.recommend.forEach((recommends) => {
          recommends.items.forEach((item) => {
            item.count = 0;
          });
        });
        res.data.menu.forEach((menu) => {
          menu.foods.forEach((food) => {
            food.count = 0;
          });
        });
        this.shopInfo = res.data;
        this.initBScroll();
        // console.log(this.shopInfo);
      });
    },
    initBScroll() {
      this.$nextTick(() => {
        this.menuScroll = new BScroll(this.$refs.menuScroll, {
          click: true,
        });
        this.foodScroll = new BScroll(this.$refs.foodScroll, {
          probeType: 3,
          click: true,
        });
      });
    },
    selectmenu(i) {
      // console.log(i);
      let foodlist = this.$refs.foodScroll.getElementsByClassName(
        "foods-list-hook"
      );
      let el = foodlist[i];
      // 滚动到对应元素
      // console.log(el);
      this.foodScroll.scrollToElement(el, 250);
    },
  },
  created() {
    this.getDate();
  },
  components: {
    CartControll,
    ShopCar,
    Food,
  },
};
</script>

<style scoped>
